#root {
  height: 100vh;
  background-color: white;
  width: 100%;
}

.blocks {
  .block {
    border: 1px solid transparent;
    padding: 0 10px;
    margin-bottom: 15px;
    .font-size-smaller {
      font-size: 0.8rem;
    }
    .font-size-small {
      font-size: 0.9rem;
    }
    .font-size-normal {
      font-size: 1rem;
    }
    .font-size-large {
      font-size: 1.5rem;
    }
    .font-size-larger {
      font-size: 1.75rem;
    }
  }
  &.selecting {
    .block {
      &.selected {
        outline: 2px dashed rgb(77, 168, 77);
        background-color: rgb(217, 245, 217);
      }
      &:hover {
        outline: 2px dashed rgb(77, 168, 77);
      }
    }
  }
}
.boolean-input {
  display: flex;
  width: 100%;
  .btn {
    &.btn-primary {
      width: 50%;
    }
    &.btn-outline-secondary {
      width: 50%;
    }
    width: 50%;
  }
}
.actions {
  .btn-group {
    .btn {
      width: 80px;
    }
  }
}
.question-title {
  font-size: 1.2rem;
  margin-bottom: 10px;
}
.grouped-container {
  &.border {
    padding: 4px;
    margin-bottom: 5px;
    &:last-child {
      margin-bottom: 0;
    }
  }
  .group-title {
    padding-right: 5px;
    opacity: 0;
    transition: opacity 0.5s ease-in-out;
    &.visible {
      opacity: 1;
    }
  }
  .grouped-row {
    align-items: center;
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    padding-bottom: 5px;
    padding-top: 5px;
    &:first-child {
      margin-top: 5px;
    }
    &:last-child {
      margin-bottom: 0;
    }
    &.active {
      outline: 2px dashed #4da84d;
    }
    .grouped-column {
      flex-grow: 0;
      padding-left: 4px;
      padding-right: 4px;
      &:first-child {
        padding-left: 0;
      }
      &:last-child {
        padding-right: 0;
      }
      &.column-label {
        text-align: right;
        width: 40%;
      }
      &.column-input {
        width: 60%;
        &.full-width {
          width: 100%;
        }
      }
    }
  }
}
